.count-of-products{
	position: absolute;
	top: -13px;
	right: -13px;
	width: 25px;
	height: 25px;
	border-radius: 50%;
	overflow: hidden;
	background-color: $purple;
	display: flex;
	align-items: center;
	justify-content: center;
}

.product-top{
	width: 100%;
	background-image: url("../img/content/store/product-top-items.png"),
	url("../img/content/store/product-top-bg.jpg");
	background-size: contain;
	background-repeat: no-repeat no-repeat;
	background-position: bottom right 60px,0 0;
	&__title{
		color: #fefefe;
		font-size: 40px;
		font-family: $Roboto-B;
		line-height: 1;
		padding-left: 100px;
		padding-top: 28px;
		padding-bottom: 42px;
		.small-text{
			font-size: 18px;
			font-family: $Roboto-R;
			line-height: 1;
			margin-bottom: 10px;
		}
	}
}


.product-wrap{
	padding: 0 30px;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	background-color: #fff;

	&__left,
	&__right{
		flex: 1;
	}

	&__right{
		margin-left: 30px;
	}
}

.product-large{
	&__pic{
		padding: 75px 35px 0 5px;
	}
}

.product-main{
	
	&_tips{
		.product-info__desc{
			margin-bottom: 0;
		}
		.accordeon__item:first-of-type{
			border-bottom: 0;
		}
		.accordeon__item + .accordeon__item{
			margin-top: 30px;
			border-bottom: 1px solid #d6d8da;
		}
	} //end tips
}

.product-info{

	&__top{
		margin-top: 50px;
		margin-bottom: 30px;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
	}
	&__general{
		flex: 1;
		line-height: 1;
		&__type{
			font-size: 18px;
			color: $purple;
			text-transform: uppercase;
		}
		&__title{
			font-size: 40px;
			font-family: $Roboto-B;
			margin-bottom: 10px;
		}
		&__price{
			font-size: 30px;
			color: $purple;
			margin-bottom: 20px;
		}
		&__available{
			background-image: -moz-linear-gradient( 90deg, #73b145 0%, #8fcd61 100%);
			background-image: -webkit-linear-gradient( 90deg, #73b145 0%, #8fcd61 100%);
			background-image: -ms-linear-gradient( 90deg, #73b145 0%, #8fcd61 100%);
			box-shadow: 0px 1px 0px 0px rgba(0, 0, 0, 0.08);
			font-size: 14px;
			color: #1f4900;
			font-family: $Roboto-B;
			padding: 8px 12px;
			border-radius: 3px;
			display: inline-block;
		}
	}



	&__user{
		flex: 1;
		display: flex;
		justify-content: flex-end;
		
		&__block{

		}
		&__link{
			display: block;
			color: inherit;
			display: flex;
			align-items: center;
			overflow: hidden;
			border-radius: 3px;

		}
		&__pic{
			background-color: #fff;
			height: 40px;
			width: 60px;
			text-align: center;
			position: relative;
			background-: -moz-linear-gradient(90deg, #ab943a 0%, #f1da80 100%);
			background: -webkit-linear-gradient(90deg, #ab943a 0%, #f1da80 100%);
			background: -ms-linear-gradient(90deg, #ab943a 0%, #f1da80 100%);
			> img {
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
			}
		}
		&__count{
			border: 1px solid #d6d6d6;
			border-left: none;
			padding: 10px 20px;
			font-family: 	$Roboto-B;
			font-size: 14px;
		}
	}

	&__desc{
		color: #636363;
		font-size: 14px;
		margin-bottom: 30px;
	}
}

.warning-block{
	border-style: solid;
	border-width: 1px;
	border-color: rgb(200, 187, 134);
	border-radius: 3px;
	background-color: rgb(255, 255, 226);
	box-shadow: inset 0px 2px 2px 0px rgba(94, 89, 46, 0.13);
	padding: 30px;

	&__title{
		color: #303030;
		font-size: 16px;	
		font-family: 	$Roboto-B;
		margin-bottom: 20px;	
	}
	&__text{
		color: #636363;
		font-size: 14px;
	}
}


.accordeon {
	&__item{
		border-top: 1px solid #d6d8da;
		&.active{
			.accordeon__inner{
				display: block;
			}
			.accordeon__trigger > i {
				transform: rotate(180deg);
			}
		}
		&:first-of-type {
			border-top: 0;
		}
		& + & {
			padding-top: 30px;
		}
	}

	&__trigger {
		color: #1b1b1b;
		font-size: 14px;
		font-family: $Roboto-B;
		display: block;
		position: relative;
		margin-bottom: 25px;
		.hint{
			margin-left: 10px;
		}
		> i {
			position: absolute;
			top: 0;
			right: 0;
			transition: all 0.5s;
		}
	}
	&__inner{
		display: none;
	}

}

.product-block {
	border: 1px solid #d6d8da;

	&__list{
		padding: 30px;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;

		&_colors{
			.product-block__item {
				font-size: 0;
				width: 30px;
				height: 30px;
				border-radius: 50%;
				overflow: hidden;
				
			}
		}
	}

	&__item{

		
		&.active{
			position: relative;
			&:after{
				content: '';
				width: 13px;
				height: 13px;
				display: block;
				border-radius: 50%;
				overflow: hidden;
				background-color: #2e2e2e;
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
			}
			

		}
		&_lilac{
			background-color: #6b6487;
		}
		&_green {
			background-color: #408850;
		}
		&_blue {
			background-color: #166caf;
		}
		&_orange{
			background-color: #cf763a;
		}
		&_purple{
			background-color: #7a51a8;
		}
		&_pink {
			background-color: #c14fa9;
		}
		&_dark-blue{
			background-color: #413f73;
		}
		&_platinum{
			background-color: #b0b0b0;
		}
		&_black{
			background-color: #303030;
		}
		&_tungsten{
			background-color: #818181;
		}
		&_rainbow{
			background: url("../img/icons/rainbow.png") no-repeat;
			background-size: cover;
		}
	}

	&__pic{
		width: 296px;
		height: 213px;
		margin: 0 auto;
		margin-top: 10px;
		margin-bottom: 20px;
	}
	&__name{
		color: #1b1b1b;
		font-family: $Roboto-M;
		font-size: 14px;
		text-align: center;
	}
	&__bottom{
		margin: 30px;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: center;
		
		&__select{
			flex: 1;
			&.select-block{
				margin-bottom: 0;
				padding-left: 20px;
			}
		}
		&__link{
			color: $purple;
			font-family: $Roboto-M;
			font-size: 14px;
			text-align: center;
			display: block;
			padding: 8px 20px;
			height: 38px;
			border: 1px solid #dfe2e5;
			margin-left: 15px;
		}

	}

}

.order-sum{

	&__list{
		margin-bottom: 30px;
	}
	&__item{
		padding: 28px 0;
		display: flex;
		align-items: center;
		position: relative;

		&:first-of-type{
			padding-top: 0;
		}
		&:last-of-type{
			border-bottom: 1px solid #dfe2e5;
		}
		& + & {
			border-top: 1px solid #dfe2e5;
		}

		&__pic{
			width: 58px;
		}
		&__info{
			margin-left: 35px;
			flex: 1;
			color: #636363;
			font-size: 14px;
		}
		&__name{
			font-family: $Roboto-B;
			margin-bottom: 15px;
		}
		&__desc{
			font-family: $Roboto-R;
			position: relative;
		}
		&__row{
			display: flex;
			flex-wrap: wrap;
			div{
				flex: 1;
			}
		}
	}

}

.total-block{
	&__item{
		text-align: right;
		font-size: 14px;
		line-height: 1;

		& + & {
			margin-top: 15px;
		}
		&_grand{
			font-family: $Roboto-B;
			.total-block__item__price{
				font-size: 24px;
			}
		}

		&__price{
			min-width: 140px;
			display: inline-block;
			color: $purple;
		}
	}
}

.btn-block.btn-block_product{
	padding-top: 45px;
	padding-bottom: 50px;
	.btn_submit{
		width: 100%;
	}
}

.quest-block {
	min-height: 318px;
	width: 100%;
	position: relative;
	// display: flex;
	background: url("../img/content/store/question-bg1.jpg") no-repeat;
	margin-bottom: 50px;
	&_tips{
		margin-bottom: 0;
	}
	&__pic{

	}
	&__info{
		// flex: 1;
		margin-left: 380px;
		color: #303030;
		line-height: 1;
		padding-top: 55px;

	}
	&__about{
		font-family: $Roboto-T;
		font-size: 30px;
		margin-bottom: 10px;
	}
	&__talk{
		font-family: $Roboto-T;
		font-size: 48px;
		margin-bottom: 30px;
	}

	.btn-block.btn-block_quest{
		padding: 0;
		text-align: left;

		.btn_submit{
			background-color: #e0e0e0;
			color: #232323;
			&:before{
				content: '';
				background-: -moz-linear-gradient(90deg, #383838 0%, #666666 100%);
				background: -webkit-linear-gradient(90deg, #383838 0%, #666666 100%);
				background: -ms-linear-gradient(90deg, #383838 0%, #666666 100%);
			}
		}
	}
	&__bottom{
		position: absolute;
		bottom: 30px;
		right: 30px;
		font-size: 14px;
		font-family: $Roboto-R;

		&__create{
			color: $purple;
		}
	}
}

// tips 
.bottom-info{
	font-size: 11px;
	color: #636363;
	background-color: #fff;
	padding: 30px;
	margin-bottom: 50px;
}

// starter kit

.product_form_starter-kit{

	.product-block{
		border: none;
		&__list{
			padding: 0;
		}
		&__item{
			flex: 1 45%;
			border: 1px solid #d6d8da;
			
			margin-bottom: 30px;
			&.active{
				border: 1px solid #6c54c7;
				&:after{
					display: none;
				}
			}

			&:nth-of-type(2n){
				margin-left: 20px;

			}
		}
		&__connection{
			padding: 15px;
			&__item{
			display: flex;
			}
			&__desc{
				margin-left: 10px;
				font-size: 14px;
			}
			&__name{
				font-family: $Roboto-B;
				color: #1b1b1b;
			}
			&__text{
				font-family: $Roboto-R;
				color: #636363;
			}
		}
		&__syringe{
			position: relative;
			padding-left: 15px;
			padding-right: 15px;
			padding-top: 41px;
			padding-bottom: 41px;
			&__text{
				font-family: $Roboto-R;
				color: #636363;
			}
			&__name{
				font-family: $Roboto-B;
				color: #1b1b1b;
			}
			&__pic{
				position: absolute;
				bottom: 0;
				right: 25px;
				img{
					display: block;
				}
			}
		}

	}
}