body,
html {
  width: 100%;
  height: 100%; }

body {
  position: relative;
  font-size: 13px;
  line-height: 1.42;
  -webkit-font-smoothing: antialiased;
  font-family: 'Roboto-Regular', 'Arial', sans-serif;
  background: #fff;
  color: #232323; }

.wrapper {
  position: relative;
  overflow: hidden;
  width: 100%;
  background-color: #e6e6e6;
  background-image: url("../img/content/bg.png");
  background-position: center 90px;
  background-repeat: no-repeat;
  background-size: contain; }
  .wrapper_home {
    background-size: 1600px 780px; }

.container-wrap {
  margin: 0 auto;
  position: relative;
  width: 1170px; }

.btn {
  display: inline-block;
  color: #000;
  background-color: #fff;
  outline: none;
  border: none;
  cursor: pointer;
  font-family: "Arial", sans-serif;
  font-size: 14px;
  font-weight: normal;
  line-height: 1;
  text-decoration: none;
  text-align: center;
  vertical-align: middle;
  margin: 0;
  padding: 10px; }

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  font-weight: normal;
  line-height: 1; }

ul {
  padding: 0;
  margin: 0; }
  ul li {
    list-style: none; }

p {
  padding: 0;
  margin: 0; }

img {
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none;
  cursor: pointer;
  color: #232323; }

* {
  box-sizing: border-box; }

.red {
  color: #ff2121; }

.white {
  color: #fff; }

.bold {
  font-weight: bold; }

.purple {
  color: #6c54c7; }

.circle {
  width: 29px;
  height: 29px;
  display: inline-block;
  text-align: center;
  line-height: 27px;
  border-radius: 50%;
  border: 1px solid #777777; }

.float-hint {
  border: none;
  outline: none;
  padding: 0; }

.input-block {
  padding-left: 10px; }

.radio-block {
  display: flex;
  align-items: center; }
  .radio-block .input {
    height: auto; }
  .radio-block .input-radio {
    margin-right: 10px; }

.radio-wrap {
  display: flex;
  align-items: center;
  margin-left: 20px; }

.form__block_add {
  display: flex;
  justify-content: flex-end;
  align-items: center; }
  .form__block_add .addDistrib {
    padding: 13px 20px;
    border: 1px solid #d6d8da; }
  .form__block_add .hint-text {
    flex: 1; }

.hint {
  width: 18px;
  height: 18px;
  vertical-align: middle;
  margin-left: 5px;
  display: inline-block;
  cursor: pointer;
  padding: 0;
  margin: 0;
  background-color: #fff;
  border: none;
  outline: none;
  background-image: url("../img/icons/hint.png");
  background-size: cover;
  position: relative;
  top: -2px;
  border-radius: 50%;
  overflow: hidden; }

.cancel {
  position: absolute;
  right: 0;
  top: 0;
  width: 10px;
  height: 10px;
  opacity: 0.3; }

.cancel:hover {
  opacity: 1; }

.cancel:before,
.cancel:after {
  position: absolute;
  left: 4px;
  content: ' ';
  height: 13px;
  width: 2px;
  background-color: #333; }

.cancel:before {
  transform: rotate(45deg); }

.cancel:after {
  transform: rotate(-45deg); }

.header {
  color: #fff; }
  .header-top {
    background-color: #222222;
    height: 90px; }
    .header-top .container-wrap {
      display: -webkit-flex;
      display: -moz-flex;
      display: -ms-flex;
      display: -o-flex;
      display: flex;
      justify-content: space-between;
      align-items: center;
      height: 100%; }

.logo {
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  align-items: center; }
  .logo__img {
    display: block;
    margin-right: 15px;
    max-width: 205px; }
  .logo__link {
    display: block; }
  .logo__text {
    color: #8e9093;
    font-size: 14px; }
    .logo__text_caption {
      margin-left: 10px;
      font-size: 11px;
      text-transform: uppercase; }

.cart-block,
.sign-in {
  display: inline-block;
  vertical-align: middle;
  height: 40px; }

.cart-block {
  border: 1px solid #fff;
  position: relative; }
  .cart-block__link {
    display: block;
    color: inherit; }
